<template>
  <modal
    :title="newDevice ? t('name-this-device') : t('edit-device')"
    v-model:active="showModal"
    :primaryButtonText="!!errorMessage ? t('ok') : newDevice ? t('add-device') : t('rename-device')"
    :onPrimaryButton="onEditDevice"
    :onCancelButton="onCancel"
    :info="!!errorMessage"
  >
    <loading v-if="editPending" :active="editPending" />
    <div>{{ newDevice ? t('give-this-device-a-name') : t('rename-this-device') }}</div>
    <div class="label input-box-title">{{ newDevice ? t('device-name') : t('new-device-name') }}</div>
    <input
      type="text"
      class="input"
      :class="{ 'is-danger': validationError }"
      v-model="newDeviceName"
      @input="clearErrors"
      part="input"
    />
    <div class="message is-danger">{{ validationError }}</div>
    <div v-if="errorMessage" class="text is-danger">{{ errorMessage }}</div>
  </modal>
</template>
<script lang="ts">
import { defineComponent, computed, PropType, ref, onUpdated, onMounted } from 'vue'
import Modal from '@/ui-components/Modal.vue'
import useEditDevice from '../composables/useEditDevice'
import { PassageDevice } from '@passageidentity/passage-js'
import { useLocale } from '@/composables/useLocale'

export default defineComponent({
  name: 'Edit Device',
  components: {
    Modal,
  },
  props: {
    deviceObject: {
      type: Object as PropType<PassageDevice>,
      required: false,
    },
    active: {
      type: Boolean,
      required: true,
    },
    newDevice: {
      type: Boolean,
      default: false,
    },
    appId: {
      type: String,
      required: true,
    },
  },
  emits: ['dialogClose', 'update:active'],
  setup(props, context) {
    const { t } = useLocale()
    const showModal = computed<boolean>({
      get(): boolean {
        return props.active
      },
      set(newValue: boolean) {
        context.emit('update:active', newValue)
      },
    })

    function clearErrors() {
      validationError.value = ''
      errorMessage.value = ''
    }

    async function onCancel() {
      context.emit('dialogClose')
      clearErrors()
    }

    const validationError = ref<string>('')
    const newDeviceName = ref<string>('')
    onUpdated(() => {
      newDeviceName.value = props.deviceObject?.friendly_name ?? ''
    })
    onMounted(() => {
      newDeviceName.value = props.deviceObject?.friendly_name ?? ''
    })

    const { renameDevice, editPending, errorMessage } = useEditDevice(props.appId)
    async function onEditDevice(): Promise<boolean> {
      if (!props.deviceObject) {
        return false
      }

      if (errorMessage.value) {
        context.emit('dialogClose')
        clearErrors()
        return true
      }

      if (newDeviceName.value === props.deviceObject.friendly_name) {
        context.emit('dialogClose')
        return true
      }

      if (!newDeviceName.value) {
        validationError.value = t('device-name-is-required')
        return false
      }

      if (newDeviceName.value.length > 64) {
        validationError.value = t('device-name-maximum')
        return false
      }

      const result = await renameDevice(props.deviceObject.id, newDeviceName.value)
      if (result) {
        context.emit('dialogClose')
        return true
      }
      return result
    }

    return {
      showModal,
      onEditDevice,
      editPending,
      validationError,
      clearErrors,
      errorMessage,
      newDeviceName,
      onCancel,
      t,
    }
  },
})
</script>
