<template>
  <div class="title secondary-title">{{ t('login-info') }}</div>
  <div class="feedback" :class="message ? 'feedback-info' : ''">
    <div v-if="message">{{ message }}</div>
  </div>
  <email
    v-if="showEmail"
    :appId="appId"
    :userInfo="userInfo"
    :required="emailRequired"
    @globalMessage="globalMessage"
  />
  <phone-number
    v-if="showPhone"
    :appId="appId"
    :userInfo="userInfo"
    :required="phoneRequired"
    @globalMessage="globalMessage"
  />
</template>
<script lang="ts">
import { PassageUserInfo } from '@/api/PassageUser'
import { defineComponent, PropType, ref, computed } from 'vue'
import PhoneNumber from './PhoneNumber.vue'
import Email from './Email.vue'
import { PassageAppInfo, Identifier, RequiredIdentifier } from '@passageidentity/passage-js'
import { useLocale } from '@/composables/useLocale'

export default defineComponent({
  name: 'UserInfo',
  components: {
    PhoneNumber,
    Email,
  },
  props: {
    userInfo: {
      type: Object as PropType<PassageUserInfo>,
      required: false,
    },
    appId: {
      type: String,
      required: true,
    },
    appInfo: {
      type: Object as PropType<PassageAppInfo>,
      required: true,
    },
  },
  setup(props) {
    const { t } = useLocale()
    const message = ref<string>('')
    function globalMessage(recievedMessage: string) {
      message.value = recievedMessage
    }

    // const requiredIdentifier = props.appInfo.required_identifier

    const showEmail = computed<boolean>(() => {
      return props.appInfo.allowed_identifier !== Identifier.phone
    })
    const showPhone = computed<boolean>(() => {
      return props.appInfo.allowed_identifier !== Identifier.email
    })
    const emailRequired = computed<boolean>(() => {
      return (
        props.appInfo.required_identifier === RequiredIdentifier.Email ||
        props.appInfo.required_identifier === RequiredIdentifier.Both
      )
    })
    const phoneRequired = computed<boolean>(() => {
      return (
        props.appInfo.required_identifier === RequiredIdentifier.Phone ||
        props.appInfo.required_identifier === RequiredIdentifier.Both
      )
    })

    return {
      message,
      globalMessage,
      showEmail,
      showPhone,
      emailRequired,
      phoneRequired,
      t,
    }
  },
})
</script>
