import { useLocale } from '@/composables/useLocale'
import { Passage, PassageDevice } from '@passageidentity/passage-js'
import { Ref, ref } from 'vue'

interface IRevokeDevice {
  revokeDevice: (device: PassageDevice) => Promise<boolean>
  revokePending: Ref<boolean>
  errorMessage: Ref<string>
}

export default function useRevokeDevice(appId: string): IRevokeDevice {
  const { t } = useLocale()
  const passageUser = new Passage(appId).getCurrentUser()
  const revokePending = ref<boolean>(false)
  const errorMessage = ref<string>('')

  async function revokeDevice(device: PassageDevice): Promise<boolean> {
    revokePending.value = true
    try {
      await passageUser.deleteDevice(device)
      return true
    } catch {
      errorMessage.value = t('failed-to-revoke-this-device')
      return false
    } finally {
      revokePending.value = false
    }
  }

  return {
    revokeDevice,
    revokePending,
    errorMessage,
  }
}
