<template>
  <form @submit.prevent="save" v-on:keyup.esc="cancel">
    <div class="login-info-row" :class="{ isCompact: isCompact }" ref="container">
      <div class="user-info-label">{{ title }}</div>
      <div class="login-info-control"><slot></slot></div>
      <div class="edit-save-container" v-if="editable">
        <div v-if="!isEditing" class="edit-save-text" @click.prevent="isEditingValue = true">{{ t('edit') }}</div>
        <div v-if="isEditing" class="edit-save-text" @click.prevent="save">{{ t('save') }}</div>
        <div v-if="isEditing" class="edit-save-text" @click.prevent="cancel">{{ t('cancel') }}</div>
      </div>
      <div class="message is-danger">
        <div v-if="errorMessage">{{ errorMessage }}</div>
      </div>
    </div>
  </form>
</template>
<script lang="ts">
import { useLocale } from '@/composables/useLocale'
import { defineComponent, PropType, ref, computed, onMounted, onBeforeUnmount } from 'vue'

export default defineComponent({
  name: 'Editable Control',
  props: {
    title: {
      type: String,
      required: true,
    },
    editable: {
      type: Boolean,
      default: true,
    },
    onSave: {
      type: Function as PropType<() => Promise<boolean>>,
      required: false,
    },
    onCancel: {
      type: Function as PropType<() => Promise<void>>,
      required: false,
    },
    isEditing: {
      type: Boolean,
      default: undefined,
    },
    errorMessage: {
      type: String,
      required: false,
    },
  },
  emits: ['update:isEditing', 'editStart'],
  setup(props, context) {
    const { t } = useLocale()
    const isEditingLocal = ref<boolean>(false)
    const isEditingValue = computed<boolean>({
      get(): boolean {
        if (props.isEditing === undefined) {
          return isEditingLocal.value
        }
        return props.isEditing
      },
      set(newValue): void {
        if (props.isEditing === undefined) {
          isEditingLocal.value = newValue
          return
        }
        context.emit('update:isEditing', newValue)
        if (newValue) {
          context.emit('editStart')
        }
      },
    })

    function save(): void {
      if (props.onSave === undefined) {
        isEditingValue.value = false
        return
      }
      props.onSave().then((validated) => {
        if (validated) {
          isEditingValue.value = false
        }
      })
    }

    function cancel(): void {
      if (props.onCancel === undefined) {
        isEditingValue.value = false
        return
      }
      props.onCancel().then(() => {
        isEditingValue.value = false
      })
    }
    const container = ref<HTMLElement>()
    const isCompact = ref<boolean>(false)
    function checkSize() {
      if (container.value === undefined) {
        return
      }
      const width = container.value.getBoundingClientRect().width
      isCompact.value = width < 415
    }

    const resizeObserver = new ResizeObserver(() => {
      checkSize()
    })

    onMounted(() => {
      if (container.value) {
        resizeObserver.observe(container.value)
      }
    })

    onBeforeUnmount(() => {
      resizeObserver.disconnect()
    })

    return {
      isEditingValue,
      save,
      cancel,
      container,
      isCompact,
      t,
    }
  },
})
</script>
