<template>
  <loading v-if="updatePending" :active="updatePending" />
  <editable-control
    :title="title"
    v-model:isEditing="isEditing"
    :errorMessage="updateEmailError"
    @editStart="onEdit"
    :onCancel="cancel"
    :onSave="save"
    :editable="!!userInfo"
  >
    <input
      type="text"
      class="input short"
      :class="{ 'is-danger': updateEmailError }"
      part="input"
      :readonly="!isEditing"
      v-model="email"
      @input="updateEmailError = ''"
      inputmode="email"
      autocomplete="email"
    />
  </editable-control>
</template>
<script lang="ts">
import { useLocale } from '@/composables/useLocale'
import { isEmail } from '@/utils/formValidation'
import { PassageUserInfo } from '@passageidentity/passage-js'
import { computed, defineComponent, PropType, ref } from 'vue'
import useUpdateUser from '../composables/useUpdateUser'
import EditableControl from './EditableControl.vue'

export default defineComponent({
  name: 'Phone Number',
  components: {
    EditableControl,
  },
  props: {
    userInfo: {
      type: Object as PropType<PassageUserInfo>,
      required: false,
    },
    appId: {
      type: String,
      required: true,
    },
    required: {
      type: Boolean,
      required: true,
    },
  },
  emits: ['globalMessage'],
  setup(props, context) {
    const { t } = useLocale()
    const isEditing = ref<boolean>(false)
    const { changeEmailRequest, updateInfoMessage, updateEmailError, updatePending, identifierExists } = useUpdateUser(
      props.appId,
    )

    const title = computed<string>(() => {
      const translated = t('email')
      return translated.charAt(0).toUpperCase() + translated.slice(1)
    })

    function initialValue(value: string | undefined): string {
      if (!value) {
        return DefaultValue.NONE
      }
      return value
    }

    enum DefaultValue {
      NONE = 'N/A',
    }
    const email = ref<string>(initialValue(props.userInfo?.email))
    let emailCopy = email.value

    function displayEmailOnEdit() {
      if (email.value === DefaultValue.NONE) {
        email.value = ''
      }
    }

    function onEdit() {
      displayEmailOnEdit()
    }

    async function cancel(): Promise<void> {
      updateEmailError.value = ''
      email.value = emailCopy
    }

    async function handleEmailInputRequest(inputEmail: string, userEmail: string): Promise<boolean> {
      if (userEmail !== inputEmail) {
        const formattedEmail = inputEmail.trim().toLowerCase()

        if (formattedEmail && !isEmail(email.value)) {
          updateEmailError.value = t('invalid-email')
          return false
        }

        if (formattedEmail) {
          const allowedIdentifier = await identifierExists(formattedEmail).catch(() => {
            updateEmailError.value = t('something-went-wrong')
            updatePending.value = false
          })
          if (updateEmailError.value) {
            return false
          }
          if (!allowedIdentifier) {
            updateEmailError.value = t('email-address-already-in-use')
            return false
          }
        }

        await changeEmailRequest(formattedEmail)
        if (updateEmailError.value === '') {
          context.emit('globalMessage', updateInfoMessage.value)
          emailCopy = inputEmail
          return true
        }
      }
      return false
    }

    function setEmptyStrings() {
      if (!email.value.trim()) {
        email.value = DefaultValue.NONE
      }
    }

    async function save(): Promise<boolean> {
      const userEmail = props.userInfo?.email ?? ''
      const inputEmail = email.value === DefaultValue.NONE ? '' : email.value
      updateEmailError.value = ''

      if (props.required && !inputEmail.trim()) {
        updateEmailError.value = t('email-is-required')
        return false
      }

      if (userEmail === inputEmail.trim()) {
        setEmptyStrings()
        return true
      }

      await handleEmailInputRequest(inputEmail, userEmail)

      if (!updateEmailError.value) {
        setEmptyStrings()
        return true
      }
      return false
    }

    return {
      isEditing,
      email,
      cancel,
      save,
      onEdit,
      updateEmailError,
      updatePending,
      title,
    }
  },
})
</script>
