<template>
  <div class="table-container" :style="`min-height: ${minHeight}px`" ref="tableContainerRef">
    <loading v-if="refreshing" :active="refreshing" />
    <DataTable
      :value="userInfo?.webauthn_devices ?? []"
      :paginator="true"
      :rows="5"
      :alwaysShowPaginator="false"
      showGridlines
      paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
      currentPageReportTemplate="Showing {first} to {last} of {totalRecords}"
      :globalFilterFields="['type']"
      sortField="last_login_at"
      :sortOrder="-1"
      dataKey="id"
      responsiveLayout="scroll"
    >
      <template v-slot:empty>
        <div class="title secondary-title">{{ t('no-devices-found') }}</div>
        <div>{{ t('there-are-no-devices-registered') }}</div>
      </template>
      <template v-slot:header>
        <div class="title secondary-title">{{ t('my-devices') }}</div>
        <div class="spacer"></div>
        <button
          class="button small"
          v-if="userInfo && webauthnAllowed && !currentDevice"
          @click.prevent="showAddModal = true"
        >
          {{ t('add-device') }}
        </button>
        <div class="buttons">
          <button class="button small" v-if="selectedDevice" @click.prevent="showEditModal = true">
            {{ t('edit-device') }}
          </button>
          <button class="button small" v-if="selectedDevice" @click.prevent="showRevokeModal = true">
            {{ t('revoke-device') }}
          </button>
        </div>
      </template>
      <Column field="id" header="">
        <template v-slot:body="{ data }">
          <checkbox :modelValue="selectedDevice?.id === data.id" @change="checkboxCheck($event, data)" />
        </template>
      </Column>
      <Column field="friendly_name" :header="t('name')" :sortable="true">
        <template v-slot:body="{ data }">
          {{ data.friendly_name }}
          <div v-if="currentDevice?.id === data.id" class="current-device">{{ t('current-device') }}</div>
        </template>
      </Column>
      <Column field="created_at" :header="t('added-time')" :sortable="true">
        <template v-slot:body="{ data }">
          {{ dayjs(data.created_at).format('MMM D, YYYY') }}
        </template>
      </Column>
      <Column field="last_login_at" :header="t('last-login')" :sortable="true">
        <template v-slot:body="{ data }">
          {{ dayjs(data.last_login_at).format('MMM D, YYYY [at] h:mm A') }}
        </template>
      </Column>
    </DataTable>
    <add-device v-model:active="showAddModal" :app-id="appId" @dialogClose="actionFinished" />
    <revoke-device
      v-if="selectedDevice"
      v-model:active="showRevokeModal"
      :device="selectedDevice"
      :currentDeviceId="currentDevice?.id"
      @dialogClose="actionFinished"
      :app-id="appId"
    />
    <edit-device
      v-model:active="showEditModal"
      :app-id="appId"
      :deviceObject="selectedDevice"
      @dialogClose="actionFinished"
    />
  </div>
</template>
<script lang="ts">
import { PassageUserInfo } from '@/api/PassageUser'
import { PassageDevice } from '@passageidentity/passage-js'
import { defineComponent, PropType, ref, onMounted, onUpdated, computed } from 'vue'
import DataTable from 'primevue/datatable'
import Column from 'primevue/column'
import dayjs from 'dayjs'
import Checkbox from '@/ui-components/Checkbox.vue'
import RevokeDevice from './RevokeDevice.vue'
import AddDevice from './AddDevice.vue'
import EditDevice from './EditDevice.vue'
import Loading from '@/ui-components/Loading.vue'
import { useLocale } from '@/composables/useLocale'

export default defineComponent({
  name: 'DevicesTable',
  components: {
    DataTable,
    Column,
    Checkbox,
    RevokeDevice,
    AddDevice,
    EditDevice,
    Loading,
  },
  props: {
    userInfo: {
      type: Object as PropType<PassageUserInfo>,
      required: false,
    },
    appId: {
      type: String,
      required: true,
    },
    refreshDevices: {
      type: Function as PropType<() => Promise<void>>,
      required: true,
    },
    webauthnAllowed: {
      type: Boolean,
      required: true,
    },
  },
  setup(props) {
    const { t } = useLocale()
    const tableContainerRef = ref()
    const minHeight = ref<number>(0)
    const showRevokeModal = ref<boolean>(false)
    const showAddModal = ref<boolean>(false)
    const showEditModal = ref<boolean>(false)

    function calculateMinHeight() {
      if (props.userInfo === undefined) {
        minHeight.value = 0
        return
      }
      if (tableContainerRef.value === null || tableContainerRef.value === undefined) {
        minHeight.value = 0
        return
      }
      if (props.userInfo.webauthn_devices.length > 5) {
        const containerHeight = (tableContainerRef.value as HTMLElement).getBoundingClientRect().height
        minHeight.value = Math.ceil(containerHeight)
      }
    }

    const selectedDevice = ref<PassageDevice>()
    function checkboxCheck(checked: boolean, device: PassageDevice): void {
      if (checked) {
        selectedDevice.value = device
        return
      }
      selectedDevice.value = undefined
    }

    onUpdated(() => {
      calculateMinHeight()
    })

    onMounted(async () => {
      calculateMinHeight()
    })

    const currentDevice = computed<PassageDevice | undefined>(() => {
      if (props.userInfo === undefined) {
        return undefined
      }
      const psgCredObj = localStorage.getItem('psg_cred_obj')
      if (!psgCredObj) {
        return undefined
      }
      const psgCredID = JSON.parse(psgCredObj)[props.userInfo?.id ?? '']
      return props.userInfo.webauthn_devices.find((device) => device.cred_id === psgCredID)
    })

    const refreshing = ref<boolean>(false)
    async function actionFinished(): Promise<void> {
      selectedDevice.value = undefined
      refreshing.value = true
      await props.refreshDevices()
      refreshing.value = false
    }

    return {
      dayjs,
      minHeight,
      tableContainerRef,
      selectedDevice,
      checkboxCheck,
      showRevokeModal,
      showAddModal,
      showEditModal,
      actionFinished,
      refreshing,
      currentDevice,
      t,
    }
  },
})
</script>
