<template>
  <loading v-if="appInfoLoading" :active="appInfoLoading" />
  <div v-else-if="invalidAppId" class="notification is-danger has-text-centered">
    Passage-Profile Error: Invalid App ID.
  </div>
  <profile-view v-if="appInfo" :appId="appId" :appInfo="appInfo" :webauthnAllowed="webauthnAllowed" />
</template>
<script lang="ts">
import { defineComponent } from 'vue'
import ProfileView from './ProfileView.vue'
import Loading from '@/ui-components/Loading.vue'
import { useAppInfo } from '@/composables/useAppInfo'

export default defineComponent({
  name: 'Profile',
  components: {
    ProfileView,
    Loading,
  },
  props: {
    appId: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const { appInfo, loading: appInfoLoading, invalidAppId, webauthnAllowed } = useAppInfo(props.appId)
    return {
      invalidAppId,
      appInfoLoading,
      appInfo,
      webauthnAllowed,
    }
  },
})
</script>
