<template>
  <loading v-if="updatePending" :active="updatePending" />
  <editable-control
    :title="title"
    v-model:isEditing="isEditing"
    :errorMessage="updatePhoneError"
    @editStart="onEdit"
    :onCancel="cancel"
    :onSave="save"
    :editable="!!userInfo"
  >
    <input
      type="text"
      id="phone"
      class="input short"
      :class="{ 'is-danger': updatePhoneError }"
      part="input"
      v-model="phone"
      :readonly="!isEditing"
      @input="updatePhoneError = ''"
      ref="phoneInputBox"
      inputmode="tel"
      autocomplete="tel"
    />
    <div ref="controlContainer" style="width: 100%"></div>
  </editable-control>
</template>
<script lang="ts">
import { useDefaultCountryCode } from '@/composables/useDefaultCountryCode'
import { useLocale } from '@/composables/useLocale'
import { PassageUserInfo } from '@passageidentity/passage-js'
import intlTelInput from 'intl-tel-input'
import { computed, defineComponent, PropType, ref, watch } from 'vue'
import useUpdateUser from '../composables/useUpdateUser'
import EditableControl from './EditableControl.vue'

export default defineComponent({
  name: 'Phone Number',
  components: {
    EditableControl,
  },
  props: {
    userInfo: {
      type: Object as PropType<PassageUserInfo>,
      required: false,
    },
    appId: {
      type: String,
      required: true,
    },
    required: {
      type: Boolean,
      required: true,
    },
  },
  emits: ['globalMessage'],
  setup(props, context) {
    const { t } = useLocale()
    const isEditing = ref<boolean>(false)
    const { changePhoneRequest, updateInfoMessage, updatePhoneError, updatePending, identifierExists } = useUpdateUser(
      props.appId,
    )

    const title = computed<string>(() => {
      const translated = t('phone-number')
      return translated.charAt(0).toUpperCase() + translated.slice(1)
    })

    function initialValue(value: string | undefined): string {
      if (!value) {
        return DefaultValue.NONE
      }
      return value
    }

    enum DefaultValue {
      NONE = 'N/A',
    }
    const phone = ref<string>(initialValue(props.userInfo?.phone))
    let phoneCopy = phone.value
    let originalCountryCode = ''
    const phoneInputBox = ref()
    const controlContainer = ref()

    const { defaultCountryCode } = useDefaultCountryCode()
    let phoneInput: intlTelInput.Plugin | undefined
    function showCountryCode() {
      phoneInput = intlTelInput(phoneInputBox.value, {
        initialCountry: phoneInput?.getSelectedCountryData().iso2 ?? defaultCountryCode.value,
        utilsScript: 'https://cdnjs.cloudflare.com/ajax/libs/intl-tel-input/17.0.8/js/utils.js',
        dropdownContainer: controlContainer.value,
        autoPlaceholder: 'aggressive',
      })
    }

    watch(phoneInputBox, () => {
      showCountryCode()
    })

    function displayPhoneOnEdit() {
      if (phone.value === DefaultValue.NONE) {
        phone.value = ''
      } else {
        phone.value = phone.value.replace(`+${phoneInput?.getSelectedCountryData().dialCode}`, '')
      }
    }

    function onEdit() {
      if (phoneInput) {
        originalCountryCode = phoneInput.getSelectedCountryData().iso2
      }
      displayPhoneOnEdit()
    }
    async function cancel(): Promise<void> {
      updatePhoneError.value = ''
      phone.value = phoneCopy
      if (phoneInput) {
        phoneInput.setCountry(originalCountryCode)
        phone.value = phone.value.replace(`+${phoneInput?.getSelectedCountryData().dialCode}`, '')
      }
    }

    async function handlePhoneInputRequest(inputPhone: string, userPhone: string): Promise<boolean> {
      if (phoneInput) {
        const formattedInputPhone = phoneInput?.getNumber()

        if (formattedInputPhone && !phoneInput?.isValidNumber()) {
          updatePhoneError.value = t('invalid-phone')
          return false
        }
        if (userPhone === formattedInputPhone) {
          return false
        }

        if (formattedInputPhone) {
          const allowedIdentifier = await identifierExists(formattedInputPhone).catch(() => {
            updatePhoneError.value = t('something-went-wrong')
            updatePending.value = false
          })
          if (updatePhoneError.value) {
            return false
          }
          if (!allowedIdentifier) {
            updatePhoneError.value = t('phone-number-already-in-use')
            return false
          }
        }

        if (userPhone !== formattedInputPhone) {
          await changePhoneRequest(formattedInputPhone)
          if (updatePhoneError.value === '') {
            context.emit('globalMessage', updateInfoMessage.value)
            phoneCopy = inputPhone
            return true
          }
          return false
        }
      }
      return false
    }

    function setEmptyStrings() {
      if (!phone.value.trim()) {
        phone.value = DefaultValue.NONE
      }
    }

    async function save(): Promise<boolean> {
      const userPhone = props.userInfo?.phone ?? ''
      const inputPhone = phone.value === DefaultValue.NONE ? '' : phone.value
      updatePhoneError.value = ''

      if (props.required && !(phoneInput?.getNumber() ?? '')) {
        updatePhoneError.value = t('phone-number-is-required')
        return false
      }

      if (userPhone === (phoneInput?.getNumber() ?? '')) {
        setEmptyStrings()
        return true
      }

      await handlePhoneInputRequest(inputPhone, userPhone)

      if (!updatePhoneError.value) {
        setEmptyStrings()
        return true
      }
      return false
    }

    return {
      isEditing,
      phone,
      controlContainer,
      phoneInputBox,
      cancel,
      save,
      onEdit,
      updatePhoneError,
      updatePending,
      title,
      t,
    }
  },
})
</script>
