<template>
  <transition name="modal" mode="out-in">
    <div
      class="passage-modal"
      part="modal"
      v-if="isActive"
      :class="{ 'is-active': isActive }"
      role="dialog"
      v-on:keyup.enter="primaryAction"
      v-on:keyup.esc="cancelAction"
    >
      <div class="passage-modal-background"></div>
      <div class="passage-modal-card">
        <div class="title main-title" :class="{ 'danger-header': danger }">{{ title }}</div>
        <div class="passage-modal-body">
          <slot></slot>
        </div>
        <div class="passage-modal-footer">
          <button
            v-if="!cancelOnly"
            class="button is-modal"
            part="modal-button modal-primary"
            @click.prevent="primaryAction"
          >
            {{ primaryButtonText }}
          </button>
          <button
            v-if="!info"
            class="button is-secondary is-modal cancel-button"
            :class="{ 'cancel-only': cancelOnly }"
            part="modal-button modal-secondary"
            @click.prevent="cancelAction"
          >
            {{ cancelButtonText }}
          </button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script lang="ts">
import { defineComponent, ref, watchEffect, onUnmounted, PropType } from 'vue'

export default defineComponent({
  name: 'Modal',
  props: {
    active: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
    onPrimaryButton: {
      type: Function as PropType<() => Promise<boolean>>,
      required: false,
    },
    onCancelButton: {
      type: Function as PropType<() => Promise<void>>,
      required: false,
    },
    primaryButtonText: {
      type: String,
      default: 'OK',
    },
    cancelButtonText: {
      type: String,
      default: 'Cancel',
    },
    info: {
      // a one-button dialog with only primaryText
      type: Boolean,
      default: false,
    },
    cancelOnly: {
      type: Boolean,
      default: false,
    },
    danger: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const isActive = ref<boolean>(false)

    // Watch props
    watchEffect(() => {
      isActive.value = props.active
      if (isActive.value) {
        // Prevent page scroll
        document.documentElement.classList.add('is-clipped')
      } else {
        // Allow page scroll
        document.documentElement.classList.remove('is-clipped')
      }
    })

    const closeModal = (): void => {
      isActive.value = false
    }

    function primaryAction(): void {
      if (props.onPrimaryButton === undefined) {
        context.emit('update:active', false)
        return
      }
      props.onPrimaryButton().then((closeDialog: boolean) => {
        if (closeDialog) {
          context.emit('update:active', false)
        }
      })
    }

    function cancelAction(): void {
      if (props.onCancelButton === undefined) {
        context.emit('update:active', false)
        return
      }
      props.onCancelButton().then(() => {
        context.emit('update:active', false)
      })
    }

    onUnmounted(() => document.documentElement.classList.remove('is-clipped'))

    return {
      isActive,
      closeModal,
      primaryAction,
      cancelAction,
    }
  },
})
</script>
