<template>
  <modal
    :title="t('revoke-device')"
    v-model:active="showModal"
    :primaryButtonText="!!errorMessage ? t('ok') : t('revoke-device')"
    :onPrimaryButton="onDeleteDevice"
    :info="!!errorMessage"
  >
    <loading v-if="revokePending" :active="revokePending" />
    <div v-if="!errorMessage">
      {{ t('confirm-remove-device') }}
      <b v-if="device?.id === currentDeviceId">{{ t('remove-current-device-warning') }}</b>
      {{ t('are-you-sure-you-want-to-continue') }}
    </div>
    <div v-if="errorMessage" class="text is-danger">{{ errorMessage }}</div>
  </modal>
</template>
<script lang="ts">
import { defineComponent, computed, PropType } from 'vue'
import Modal from '@/ui-components/Modal.vue'
import useRevokeDevice from '../composables/useRevokeDevice'
import { useLocale } from '@/composables/useLocale'
import { PassageDevice } from '@passageidentity/passage-js'

export default defineComponent({
  name: 'Revoke Device',
  components: {
    Modal,
  },
  props: {
    device: {
      type: Object as PropType<PassageDevice>,
      required: true,
    },
    active: {
      type: Boolean,
      required: true,
    },
    currentDeviceId: {
      type: String,
      required: false,
    },
    appId: {
      type: String,
      required: true,
    },
  },
  emits: ['dialogClose', 'update:active'],
  setup(props, context) {
    const { t } = useLocale()
    const showModal = computed<boolean>({
      get(): boolean {
        return props.active
      },
      set(newValue: boolean) {
        context.emit('update:active', newValue)
      },
    })
    const { revokeDevice, revokePending, errorMessage } = useRevokeDevice(props.appId)
    async function onDeleteDevice(): Promise<boolean> {
      if (errorMessage.value) {
        context.emit('dialogClose')
        return true
      }
      const result = await revokeDevice(props.device)
      if (result) {
        context.emit('dialogClose')
      }
      return result
    }

    return {
      showModal,
      onDeleteDevice,
      revokePending,
      errorMessage,
      t,
    }
  },
})
</script>
