<template>
  <div class="metadata-header">
    <div class="title secondary-title">{{ t('personal-info') }}</div>
    <div v-if="userInfo" class="edit-save-container">
      <div v-if="!isEditing" class="edit-save-text" @click.prevent="isEditing = true">{{ t('edit') }}</div>
      <div v-if="isEditing" class="edit-save-text" @click.prevent="save">{{ t('save') }}</div>
      <div v-if="isEditing" class="edit-save-text" @click.prevent="cancel">{{ t('cancel') }}</div>
    </div>
  </div>
  <div class="feedback" :class="updateMetadataError ? 'feedback-danger' : ''">
    <div v-if="updateMetadataError">{{ updateMetadataError }}</div>
  </div>
  <user-metadata
    v-if="userInfo"
    :appInfo="appInfo"
    :layoutType="LayoutType.Profile"
    :userMetadata="userInfo.user_metadata ?? {}"
    ref="componentRef"
    :isEditing="isEditing"
  />
  <div v-else style="text-align: center">
    <div class="title secondary-title">{{ t('no-user-found') }}</div>
    <div>{{ t('there-is-no-currently-logged-in-user') }}</div>
  </div>
</template>
<script lang="ts">
import { ComponentInternalInstance, defineComponent, PropType, ref } from 'vue'
import UserMetadata, { LayoutType } from '@/components/UserMetadata/UserMetadata.vue'
import { PassageAppInfo, PassageUserInfo } from '@passageidentity/passage-js'
import useUpdateUser from '../composables/useUpdateUser'
import { useLocale } from '@/composables/useLocale'

export default defineComponent({
  name: 'Metadata Section',
  components: {
    UserMetadata,
  },
  props: {
    appInfo: {
      type: Object as PropType<PassageAppInfo>,
      required: true,
    },
    userInfo: {
      type: Object as PropType<PassageUserInfo>,
      required: false,
    },
    refreshMetadata: {
      type: Function as PropType<() => Promise<void>>,
      required: true,
    },
  },
  setup(props) {
    const { t } = useLocale()
    const isEditing = ref<boolean>(false)
    const { updateMetadata, updateMetadataError } = useUpdateUser(props.appInfo.id)

    async function save() {
      let success = true
      if (componentRef.value) {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const isValid = (componentRef.value as any).validate()
        if (!isValid) {
          return
        }
        if (props.userInfo?.user_metadata) {
          success = await updateMetadata(props.userInfo.user_metadata, props.appInfo.user_metadata_schema)
        }
      }
      isEditing.value = !success
    }

    async function cancel() {
      await props.refreshMetadata()
      if (componentRef.value) {
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        ;(componentRef.value as any).validate()
      }
      isEditing.value = false
    }

    const componentRef = ref<ComponentInternalInstance>()

    return {
      LayoutType,
      isEditing,
      save,
      cancel,
      componentRef,
      updateMetadataError,
      t,
    }
  },
})
</script>
