import { Passage, PassageAppInfo, PassageError, PassageUserInfo, UserMetadataType } from '@passageidentity/passage-js'
import dayjs from 'dayjs'
import { onMounted, ref } from 'vue'

/* eslint-disable-next-line @typescript-eslint/explicit-function-return-type, @typescript-eslint/explicit-module-boundary-types*/
export function useCurrentUser(appInfo: PassageAppInfo) {
  const loading = ref<boolean>(true)
  const error = ref<PassageError | undefined>(undefined)
  const userInfo = ref<PassageUserInfo | undefined>(undefined)

  async function execute(): Promise<void> {
    try {
      loading.value = true
      const passageUser = new Passage(appInfo.id).getCurrentUser()
      if (passageUser) {
        userInfo.value = await passageUser.userInfo()
        if (userInfo.value !== undefined && userInfo.value.user_metadata === null) {
          userInfo.value.user_metadata = {}
        }
        transformMetadata()
      }
    } catch (err: unknown) {
      error.value = err as PassageError
    } finally {
      loading.value = false
    }
  }

  onMounted(() => {
    execute()
  })

  async function refreshDevices(): Promise<void> {
    if (!userInfo.value) {
      return
    }
    try {
      const passageUser = new Passage(appInfo.id).getCurrentUser()
      userInfo.value.webauthn_devices = await passageUser.listDevices()
    } catch (err: unknown) {
      error.value = err as PassageError
    } finally {
      loading.value = false
    }
  }

  async function refreshMetadata(): Promise<void> {
    if (!userInfo.value) {
      return
    }
    try {
      const passageUser = new Passage(appInfo.id).getCurrentUser()
      userInfo.value.user_metadata = (await passageUser.getMetadata()) ?? {}
      transformMetadata()
    } catch (err: unknown) {
      error.value = err as PassageError
    } finally {
      loading.value = false
    }
  }

  function transformMetadata(): void {
    if (appInfo === undefined) {
      return
    }
    appInfo.user_metadata_schema.forEach((schema) => {
      if (!userInfo.value || !userInfo.value.user_metadata) {
        return
      }
      const metadataValue = userInfo.value.user_metadata[schema.field_name]
      if (schema.type === UserMetadataType.DATE && metadataValue) {
        userInfo.value.user_metadata[schema.field_name] = dayjs(metadataValue as string).format('MM/DD/YYYY')
      }
    })
  }

  return {
    userInfo,
    loading,
    error,
    refreshDevices,
    refreshMetadata,
  }
}
