<template>
  <loading v-if="loading" :active="loading" />
  <div
    v-else-if="error && error.statusCode !== PassageErrorCode.PSGLoginRequired"
    class="notification is-danger has-text-centered"
  >
    Unable to get information about current user: {{ error.statusText }}
  </div>
  <user-info v-if="!loading" :appId="appId" :userInfo="userInfo" :appInfo="appInfo" />
  <div class="profile-divider" v-if="appInfo.layouts.profile.length > 0" />
  <metadata-section
    v-if="!loading && appInfo.layouts.profile.length > 0"
    :appInfo="appInfo"
    :userInfo="userInfo"
    :refreshMetadata="refreshMetadata"
  />
  <div class="profile-divider" />
  <devices-table
    v-if="!loading"
    :appId="appId"
    :userInfo="userInfo"
    :refreshDevices="refreshDevices"
    :webauthnAllowed="webauthnAllowed"
  />
</template>
<script lang="ts">
import { PassageAppInfo, PassageErrorCode } from '@passageidentity/passage-js'
import { defineComponent, PropType } from 'vue'
import UserInfo from '../components/UserInfo.vue'
import DevicesTable from '../components/DevicesTable.vue'
import MetadataSection from '../components/MetadataSection.vue'
import { useCurrentUser } from '../composables/useCurrentUser'
import Loading from '@/ui-components/Loading.vue'

export default defineComponent({
  name: 'ProfileView',
  components: {
    UserInfo,
    DevicesTable,
    MetadataSection,
    Loading,
  },
  props: {
    appId: {
      type: String,
      required: true,
    },
    appInfo: {
      type: Object as PropType<PassageAppInfo>,
      required: true,
    },
    webauthnAllowed: {
      type: Boolean,
      required: true,
    },
  },
  setup(props) {
    const { userInfo, loading, error, refreshDevices, refreshMetadata } = useCurrentUser(props.appInfo)
    return {
      userInfo,
      loading,
      error,
      refreshDevices,
      refreshMetadata,
      PassageErrorCode,
    }
  },
})
</script>
