import { useLocale } from '@/composables/useLocale'
import { Passage } from '@passageidentity/passage-js'
import { ref } from 'vue'

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export default function useEditDevice(appId: string) {
  const { t } = useLocale()
  const passageUser = new Passage(appId).getCurrentUser()
  const editPending = ref<boolean>(false)
  const errorMessage = ref<string>('')

  async function renameDevice(deviceID: string, newFriendlyName: string): Promise<boolean> {
    editPending.value = true
    try {
      await passageUser.editDevice(deviceID, { friendly_name: newFriendlyName })
      return true
    } catch {
      errorMessage.value = t('failed-to-rename-this-device')
      return false
    } finally {
      editPending.value = false
    }
  }

  return {
    renameDevice,
    editPending,
    errorMessage,
  }
}
