<script lang="ts">
import { defineComponent, Transition, h } from 'vue'

export default defineComponent({
  name: 'Loading',
  props: {
    active: Boolean,
  },
  /* eslint-disable indent */
  setup(props, { attrs }) {
    return () =>
      props.active
        ? h(
            Transition,
            {
              name: 'fade',
              mode: 'out-in',
            },
            () => {
              return [
                h(
                  'div',
                  {
                    ...attrs,
                    class: ['component-loading loading-overlay is-overlay'],
                    title: 'Loading...',
                  },
                  [h('div', { class: ['loading-background is-overlay'] }, ''), h('div', { class: ['loading-icon'] })],
                ),
              ]
            },
          )
        : null
  },
  /* eslint-enable indent */
})
</script>
