<template>
  <modal
    :title="t('add-new-device')"
    v-model:active="showAddModal"
    :cancelOnly="!errorMessage"
    :onPrimaryButton="onAddDevice"
    :primaryButtonText="t('try-again')"
  >
    <loading v-if="addPending" :active="addPending" />
    <figure class="image has-text-centered" style="margin: 5px auto">
      <inline-svg v-if="errorMessage" :src="Icons.fingerprintFailed" alt="Biometrics scan failed" />
      <platform-icon v-else alt="Biometrics scan" />
    </figure>
    <div class="has-text-centered" v-if="!errorMessage">{{ t('enable-this-device') }}</div>
    <div v-if="errorMessage" class="text is-danger has-text-centered">{{ errorMessage }}</div>
  </modal>
  <edit-device
    v-model:active="showEditModal"
    :app-id="appId"
    :deviceObject="newDevice"
    @dialogClose="editDialogClose"
    newDevice
  />
</template>
<script lang="ts">
import { defineComponent, computed, watch, ref } from 'vue'
import Modal from '@/ui-components/Modal.vue'
import useAddDevice from '../../composables/useAddDevice'
import EditDevice from './EditDevice.vue'
import { PassageDevice } from '@passageidentity/passage-js'
import { Icons } from '@/utils/Icons'
import InlineSvg from 'vue-inline-svg'
import PlatformIcon from '@/components/PlatformIcon/PlatformIcon.vue'
import { useLocale } from '@/composables/useLocale'

export default defineComponent({
  name: 'Revoke Device',
  components: {
    Modal,
    EditDevice,
    InlineSvg,
    PlatformIcon,
  },
  props: {
    active: {
      type: Boolean,
      required: true,
    },
    appId: {
      type: String,
      required: true,
    },
  },
  emits: ['dialogClose', 'update:active'],
  setup(props, context) {
    const { t } = useLocale()

    const showAddModal = computed<boolean>({
      get(): boolean {
        return props.active
      },
      set(newValue: boolean) {
        context.emit('update:active', newValue)
      },
    })

    const showEditModal = ref<boolean>(false)
    const { addDevice, addPending, errorMessage } = useAddDevice(props.appId)

    const newDevice = ref<PassageDevice>()
    async function onAddDevice(): Promise<boolean> {
      newDevice.value = await addDevice()
      if (newDevice.value) {
        showAddModal.value = false
        showEditModal.value = true
        return true
      }
      return false
    }

    watch(showAddModal, () => {
      if (showAddModal.value) {
        onAddDevice()
      }
    })

    function editDialogClose() {
      context.emit('dialogClose')
    }

    return {
      showAddModal,
      showEditModal,
      addDevice,
      errorMessage,
      addPending,
      onAddDevice,
      newDevice,
      editDialogClose,
      Icons,
      t,
    }
  },
})
</script>
