<template>
  <div id="passage-profile-container" ref="containerRef" v-show="!initialLoading">
    <passage-profile-view :key="appId" :appId="appId ?? ''" />
  </div>
</template>
<script lang="ts">
import { defineComponent, onBeforeUpdate, ref, watch } from 'vue'
import { useWebFonts } from '@/composables/useWebFonts'
import PassageProfileView from '@/PassageProfile/views/MainView.vue'
import appStyles from '../../assets/scss/app.scss'
import { useIntlTelInput } from '@/composables/useIntlTelInput'
import { setLocale } from '@/composables/useLocale'
import { useDefaultCountryCode } from '@/composables/useDefaultCountryCode'

export default defineComponent({
  name: 'ProfileApp',
  props: {
    appId: String,
    lang: String,
    defaultCountryCode: String,
  },
  components: {
    PassageProfileView,
  },
  styles: [appStyles],
  setup(props) {
    function storeProps() {
      const { updateDefaultCountryCode } = useDefaultCountryCode()
      updateDefaultCountryCode(props.defaultCountryCode)
    }
    storeProps()

    onBeforeUpdate(() => {
      storeProps()
    })

    const initialLoading = ref<boolean>(true)
    setLocale(props.lang).finally(() => {
      initialLoading.value = false
    })
    watch(
      () => props.lang,
      (previous, next) => {
        if (previous !== next) {
          setLocale(props.lang)
        }
      },
    )

    useWebFonts()
    const containerRef = ref()

    useIntlTelInput(containerRef)

    return {
      containerRef,
      initialLoading,
    }
  },
})
</script>
